.oneSong{
    color:white;
    display: flex;
    width:80%;
    margin-left:50px;
    font-weight: 100;
    padding : 20px;
    font-size:14px;
}

.songName{
    margin-left:25px;
}

.songTime{
    margin-left: auto;
}

.oneSong:hover{
    color:rgb(67, 216, 67);
    background-color:#282828;
    
    cursor: pointer;
}

.active{
    color:rgb(67, 216, 67);
    background-color:#282828;
}