body {
  overflow-x: hidden;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.col-6 {
  margin: 0 !important;
  padding: 0 !important;
}
.row {
  background-color: #15141c;
  margin: 0 !important;
  padding: 0 !important;
}

.loading{
  width:100%;
  height:100vh;
  background-color: #000000;
}

.loading>img{
  width:25%;
  margin-top:25%;
  margin-left:38%;
}

.loadingHome{
  width:100%;
  height:100vh;
  background-color: #000000;
}

.loadingHome>img{
  width:50%;
  margin-top:15%;
  margin-left:20%;
}

.playlistComponent {
  background-color: #15141c;
  height:100vh;
}
.listSong {
  position: relative;
  top : 80px;
  width: 100%;
  height:60vh;
  overflow-y: scroll;
  padding-right: 17px; /* Increase/decrease this value for cross-browser compatibility */
  box-sizing: content-box;
}
-webkit-scrollbar {
  display: none;
}
.copyRight {
  position: absolute;
  margin-top: 50px;
  margin-left: 55px;
  color: rgb(75, 74, 74);
  font-size: 13px;
  width: 80%;
}

.oneSong{
    color:white;
    display: -webkit-flex;
    display: flex;
    width:80%;
    margin-left:50px;
    font-weight: 100;
    padding : 20px;
    font-size:14px;
}

.songName{
    margin-left:25px;
}

.songTime{
    margin-left: auto;
}

.oneSong:hover{
    color:rgb(67, 216, 67);
    background-color:#282828;
    
    cursor: pointer;
}

.active{
    color:rgb(67, 216, 67);
    background-color:#282828;
}

.detailsComponent{
    width:100%;
    text-align:center;
    height:100vh;
    background-color:#15141c;
}

.image{
    text-align: center;
}

.image>img{
    margin-top:80px;
    width:55%;
    box-shadow: 2px 2px black;
    border-radius:2px;
}
.titleCover{
    width:70%;
    margin : auto;
    margin-top:15px;
    color:white;
    font-weight: 700;
    font-size:22px;
}

.singer{
    color:rgb(214, 211, 211);
    font-size: 13px;
    font-weight: 100;
}

Button{
    margin-top:35px !important;
    width:100px;
    font-weight: 100 !important;
    font-size: 12px !important;
    border-radius: 999px !important;
}

.totalSong{
    color:rgb(139, 139, 139);
    font-size: 12px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 300;
    margin-top:10px;
}

.controlCover{
    margin-top: 30px;
}

.controlType,.moreControl{
    display:inline;
}
.controlType{
    color:rgb(17, 146, 17);
    font-weight: 600;
    font-size: 11px;
}

.moreControl{
    margin-left:30px;
    color:white;
    font-weight: 600;
    font-size:20px;
}
.oneAlbum{
    margin-top: 20px;
    height:350px;
}
.albumTitle{
    width:95%;
    margin: auto;
    text-align: left;
}

.albumName{
    padding-top:15px;
    color:white;
    font-weight: 500;
    font-size:15px;
}

.albumName:hover{
    clamp : 2;
}

.albumTitle > em{
    color : gray;
}

.oneAlbum:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition-duration: 0.15s;
    cursor: pointer;
}

.category{
    color:white;
    font-size:30px;
    font-weight: 400;
    margin-top:10px;
    margin-left:20px;
}
.libContent{
    /* overflow: scroll; */
    margin-bottom:50px;
}
.menuComponent {
  width: 100%;
  background-color: black;
  height: 100vh;
}
#logo {
  width: 65%;
}
.logo {
  padding-left: 35px;
  padding-top: 20px;
}
.menu {
  color: rgb(194, 190, 190);
  font-weight: 100;
}
.menu > ul {
  margin-top: 10px;
  margin-bottom: 10px;
  padding-right: 10px;
  list-style-type: none;
  width: 90%;
  padding-left: 20px;
}

.menu > ul li {
  color: rgb(194, 190, 190);
  margin-top:10px;
  
  padding-bottom: 10px;
  padding-left: 20px;
  border-radius: 999px;
}

.menu > ul a {
  text-decoration: none;
}

.menu > ul li:hover {
  background-color: #282828;
}

.menu>ul:nth-child(1){
  margin-top:30px;
}

.menu > ul li svg {
  margin-top: 10px;
  width: 20px !important;
  margin-right: 10px;
}

.recent {
  color: rgb(124, 123, 123);
  margin-left: 35px;
  -ms-user-select: none;
      user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.sumList {
  margin-top: 15px;
}

.recentList {
  text-align: left;
  margin-top: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  width: 100%;
  border-left: 5px solid black;
}

.recentList > .details > .title {
  color: rgb(161, 160, 160);
  font-weight: 400;
  width: 90%;
}

.recentList > .details > .type {
  color: rgb(161, 160, 160);
  font-weight: 100;
}

.active > .details > .title {
  color: white;
  font-weight: 400;
}

.install {
  position: absolute;
  bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
  font-weight: 400;
}

.installIcon,
.installWord {
  display: inline-block;
}

.install>a{
  text-decoration: none;
  color: rgb(161, 160, 160);
}

.install>a:hover{
  color:white;
  font-weight: 500;
}

.installWord {
  margin-left: 15px;
}
hr {
  background-color: rgb(161, 160, 160);
}
.avatar > img {
  border-radius: 100px;
  width: 15%;
}
.avatar,
.userName {
  display: inline;
}

.userName {
  margin-left: 15px;
  color: white;
  font-weight: 600;
}

#recent > a {
  text-decoration: none;
}

#recent:hover {
  cursor: pointer;
  background-color: #282828;
}

.activeMenu {
  color: white;
  font-weight: 500;
  border-left: 5px solid green;
  background-color: #282828;
}
.playing {
  margin-bottom: 100px;
}

.controlComponent{
    position: fixed;
    bottom : 0;
    z-index: 1;
    height:10vh;
    width:100%;
    background-color:#282828;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.imageControl{
    margin:right;
    width: 100px;
}
.imageControl>img{
    margin-left: 20px;
    width:70%;
}
.detailsControl{
    width:170px;
    overflow: hidden;
    margin-left:20px;
    color:white;
    font-size:13px;
    font-weight: 500;
}
.detailsControl>singer{
    font-weight: 100;
    font-size:11px;
}
.checkButton{
    color:green;
    margin-left:20px;
}
.progressBar{
    width: 70%;
    padding-top:10px;
}
.progress-bar{
    padding-top:10px;
}
.progressFlow{
    display: -webkit-flex;
    display: flex;
    color:#494848;
}
.startTime{
    margin-right:5%;
}
.progress{
    width:100%;
    background-color:#494848 !important;
    height:5px !important;
}
.flowControl{
    color: rgb(124, 123, 123);
    width:100%;
    text-align: center;
}
.flowControl>svg{
    margin-right:23px;
    margin-bottom: 7px;
    font-size: 13px;
}
#playButton{
    font-size:25px;
}
.musicControl{
    color: rgb(124, 123, 123);
    width:100%;
    display: -webkit-flex;
    display: flex;
    text-align: center;
}
.musicControl>svg{
    margin-left:23px;
    font-size:13px;
}
#musicControlProg{
    width: 100%;
    margin-left:10px;
    margin-top:3px;
}
.flowControl>svg:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition-duration: 0.2s;
    cursor: pointer;
}
.endTime{
    margin-left:5%;
}
