.menuComponent {
  width: 100%;
  background-color: black;
  height: 100vh;
}
#logo {
  width: 65%;
}
.logo {
  padding-left: 35px;
  padding-top: 20px;
}
.menu {
  color: rgb(194, 190, 190);
  font-weight: 100;
}
.menu > ul {
  margin-top: 10px;
  margin-bottom: 10px;
  padding-right: 10px;
  list-style-type: none;
  width: 90%;
  padding-left: 20px;
}

.menu > ul li {
  color: rgb(194, 190, 190);
  margin-top:10px;
  
  padding-bottom: 10px;
  padding-left: 20px;
  border-radius: 999px;
}

.menu > ul a {
  text-decoration: none;
}

.menu > ul li:hover {
  background-color: #282828;
}

.menu>ul:nth-child(1){
  margin-top:30px;
}

.menu > ul li svg {
  margin-top: 10px;
  width: 20px !important;
  margin-right: 10px;
}

.recent {
  color: rgb(124, 123, 123);
  margin-left: 35px;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.sumList {
  margin-top: 15px;
}

.recentList {
  text-align: left;
  margin-top: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  width: 100%;
  border-left: 5px solid black;
}

.recentList > .details > .title {
  color: rgb(161, 160, 160);
  font-weight: 400;
  width: 90%;
}

.recentList > .details > .type {
  color: rgb(161, 160, 160);
  font-weight: 100;
}

.active > .details > .title {
  color: white;
  font-weight: 400;
}

.install {
  position: absolute;
  bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
  font-weight: 400;
}

.installIcon,
.installWord {
  display: inline-block;
}

.install>a{
  text-decoration: none;
  color: rgb(161, 160, 160);
}

.install>a:hover{
  color:white;
  font-weight: 500;
}

.installWord {
  margin-left: 15px;
}
hr {
  background-color: rgb(161, 160, 160);
}
.avatar > img {
  border-radius: 100px;
  width: 15%;
}
.avatar,
.userName {
  display: inline;
}

.userName {
  margin-left: 15px;
  color: white;
  font-weight: 600;
}

#recent > a {
  text-decoration: none;
}

#recent:hover {
  cursor: pointer;
  background-color: #282828;
}

.activeMenu {
  color: white;
  font-weight: 500;
  border-left: 5px solid green;
  background-color: #282828;
}
.playing {
  margin-bottom: 100px;
}
