.oneAlbum{
    margin-top: 20px;
    height:350px;
}
.albumTitle{
    width:95%;
    margin: auto;
    text-align: left;
}

.albumName{
    padding-top:15px;
    color:white;
    font-weight: 500;
    font-size:15px;
}

.albumName:hover{
    clamp : 2;
}

.albumTitle > em{
    color : gray;
}

.oneAlbum:hover{
    transform: scale(1.1);
    transition-duration: 0.15s;
    cursor: pointer;
}
