.detailsComponent{
    width:100%;
    text-align:center;
    height:100vh;
    background-color:#15141c;
}

.image{
    text-align: center;
}

.image>img{
    margin-top:80px;
    width:55%;
    box-shadow: 2px 2px black;
    border-radius:2px;
}
.titleCover{
    width:70%;
    margin : auto;
    margin-top:15px;
    color:white;
    font-weight: 700;
    font-size:22px;
}

.singer{
    color:rgb(214, 211, 211);
    font-size: 13px;
    font-weight: 100;
}

Button{
    margin-top:35px !important;
    width:100px;
    font-weight: 100 !important;
    font-size: 12px !important;
    border-radius: 999px !important;
}

.totalSong{
    color:rgb(139, 139, 139);
    font-size: 12px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 300;
    margin-top:10px;
}

.controlCover{
    margin-top: 30px;
}

.controlType,.moreControl{
    display:inline;
}
.controlType{
    color:rgb(17, 146, 17);
    font-weight: 600;
    font-size: 11px;
}

.moreControl{
    margin-left:30px;
    color:white;
    font-weight: 600;
    font-size:20px;
}