#searchBox {
  width: 100%;
  margin: 0 !important;
  padding: 0 !important;
}
.search {
  display: flex;
}
#SearchInput {
  width: 100%;
  height: 60px;
  font-size: 29px;
  font-weight: 600;
  padding-left: 50px;
  background-color: #282828;
  border: #282828;
  border-radius: 0 !important;
}

#SearchInput:focus {
  color: rgb(202, 202, 202);
  border: 0;
  outline: none;
  box-shadow: none;
  border-bottom: 1px solid green;
}

.result {
  width: 100%;
  color: rgb(202, 202, 202);
  text-align: center;
  margin-top: 120px;
}

.loadingSearch {
  text-align: center;
  margin-top: 20%;
}
.loadingSearch > img {
  opacity: 0.6;
  width: 10%;
}

.content {
  position: relative;
  text-align: center;
  margin-top: 5%;
  width: 100%;
  height: 85vh;
  overflow-y: scroll;
  padding-right: 17px; /* Increase/decrease this value for cross-browser compatibility */
  box-sizing: content-box;
}
-webkit-scrollbar {
  display: none;
}

.content a {
  text-decoration: none;
  font-size: 25px;
  font-weight: 400;
  color: rgb(202, 202, 202);
}

.col-title {
  margin-bottom: 50px;
}

.col-title:hover {
  transform: scale(1.2);
  transition-duration: 0.4s;
}

.content a:hover {
  color: rgb(18, 143, 18);
  text-decoration: none;
}

.oneAlbumSearch {
  width: 100%;
}

.oneAlbumSearch > img {
  width: 100%;
  border-radius: 5px;
  box-shadow: 5px 5px 10px black;
}

.oneAlbumSearch:hover {
  transform: scale(1.1);
  transition-duration: 0.3s;
  cursor: pointer;
}

.oneAlbumSearch:hover div {
  color: white;
  font-weight: 600;
}

.oneAlbumSearch > div {
  color: rgb(110, 109, 109);
  margin-bottom: 25px;
  margin-top: 5px;
  font-weight: 400;
}

.oneTrackSearch {
  text-align: left;
  margin: 0;
}

.oneTrackSearch > div {
  margin: 0;
  padding: 0;
  margin-left: 40px;
}

.oneTrackSearch > div:first-child {
  font-size: 21px;
}

.oneTrackSearch > div:last-child {
  font-size: 12px;
}

.oneTrackSearch > div:last-child {
  padding-bottom: 25px;
  padding-top: 5px;
}

.Error {
  color: white;
  font-size: 30px;
  margin-top: 20%;
  margin-left: 0%;
}

.col-main {
  border-right: 3px solid rgb(0, 71, 0);
}

.searchTitle {
  font-size: 30px;
  color: rgb(214, 214, 214);
  margin-bottom: 25px;
  margin-top:25px;
}
.searchTitle:not(:first-child){
  border-top: 2px solid rgb(100, 100, 100);
  padding-top:25px;
}
