.controlComponent{
    position: fixed;
    bottom : 0;
    z-index: 1;
    height:10vh;
    width:100%;
    background-color:#282828;
    display: flex;
    align-items: center;
}

.imageControl{
    margin:right;
    width: 100px;
}
.imageControl>img{
    margin-left: 20px;
    width:70%;
}
.detailsControl{
    width:170px;
    overflow: hidden;
    margin-left:20px;
    color:white;
    font-size:13px;
    font-weight: 500;
}
.detailsControl>singer{
    font-weight: 100;
    font-size:11px;
}
.checkButton{
    color:green;
    margin-left:20px;
}
.progressBar{
    width: 70%;
    padding-top:10px;
}
.progress-bar{
    padding-top:10px;
}
.progressFlow{
    display: flex;
    color:#494848;
}
.startTime{
    margin-right:5%;
}
.progress{
    width:100%;
    background-color:#494848 !important;
    height:5px !important;
}
.flowControl{
    color: rgb(124, 123, 123);
    width:100%;
    text-align: center;
}
.flowControl>svg{
    margin-right:23px;
    margin-bottom: 7px;
    font-size: 13px;
}
#playButton{
    font-size:25px;
}
.musicControl{
    color: rgb(124, 123, 123);
    width:100%;
    display: flex;
    text-align: center;
}
.musicControl>svg{
    margin-left:23px;
    font-size:13px;
}
#musicControlProg{
    width: 100%;
    margin-left:10px;
    margin-top:3px;
}
.flowControl>svg:hover{
    transform: scale(1.2);
    transition-duration: 0.2s;
    cursor: pointer;
}
.endTime{
    margin-left:5%;
}